<script>
import { nextTick } from 'vue';

export default {
  name: 'XBeesComponent',
  mounted() {
    nextTick(() => {
      const images = [
        this.$refs.image1,
        this.$refs.image2,
        this.$refs.image3,
        this.$refs.image4
      ];

      const observerOptions = {
        root: null,
        rootMargin: '0px',
        threshold: 0.3
      };

      const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            entry.target.classList.add('in-view');
            observer.unobserve(entry.target); // Pour viter de r-observer une fois l'animation dclenche
          }
        });
      }, observerOptions);

      images.forEach(image => {
        if (image) {
          observer.observe(image);
        }
      });
    });
  }
};
</script>

